@use "../utils/variables";


.pagination {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
    font-size: 12px;
    color: variables.$mediumgrey;

    .pagination-system {
        display: flex;
        align-items: center;
        fill: variables.$mediumgrey;
    }

    .pagination-btn {
        border: none;
        background-color: transparent;
        cursor: pointer;

    }

    select {
        font-size: 12px;
        color: variables.$mediumgrey;
        padding: 0px;
        border: none;
        background-color: transparent;
        cursor: pointer;
    }
}